@import "typography.css"; */

:root{
    --text-font-size: 0.5rem; 
}


body {
    background: rgb(216, 215, 215);
    margin: 0px 0px;
}

h1 {
    background: url(../imgs/sky.jpg)  no-repeat center/100%;   
    height: 70px;
    width:100%;
    object-fit: fill;
    margin-top: 0px;
    padding-top: 25px;
}
 img {
    display: block;    
    margin: 0 auto;
    max-height: 120px;
}



form {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
}

legend {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}
#submit, #nextBtn {
    display: block;
    margin: 0 auto;
    width: 100px;
    padding: 0.3rem;
    margin-bottom: 0.7rem;
}
#submit{
    margin-top: 0.7rem;
}
p#answer {
    height: 20px;
}
p#copyRight {
    margin-top: 2.5rem;
} 
@media screen and (min-width: 400px) {
    p#copyRight {
        margin-top: 10rem;
        }
}
@media screen and (min-width: 500px) {
    h1{
        font-size: 3rem;
    }
}
@media screen and (min-width: 600px) {
    form{
        width: 500px;
        margin: 0 auto;
    }
    h1{
        height: 100px;
        padding-top: 0.6rem;
    }
    p#copyRight {
        margin-top: 10rem;
        }
  }

  @media screen and (min-width: 900px) {
      h1{
        height: 120px;
      }
     
}
.scoreBoard{
    display: inline;
    border: solid 0.1rem red;
    margin: 0.5rem;
    padding: 0.2rem;
}
#scorePanel{
    background: greenyellow;
    padding: 0.5rem;
}
#resetBtn{
    display: block;
    margin: 0.5rem auto;
    margin-bottom: 0;
}

fieldset{
    border-width: 0.1rem; 
    padding: 0.3rem; 
    margin: default;
}
button{
    background-color: buttonface;
    color: buttontext;
    border-width: 2px;
    padding: 1px 6px;
    cursor: default;
}